@include for-mobile-layout {
    .sidebar-container {
        display: none;
    }
}

@include for-narrow-layout {

    .sidebar-container {
        background-color: #292f4c;
        height: 100vh;
        width: rem(89px);

        .sidebar-icon {
            width: rem(23px);
            height: rem(23px);
            font-size: rem(20px);
            margin: rem(11px);
            color: white;
            border-radius: rem(8px);
            cursor: pointer;
            transition: background-color .2s;
        }

        .sidebar-icon:hover {
            background-color: #0c0d18;
        }

        .all-nav-bar {
            height: 91vh;
        }

        .btn-home {
            margin: auto;
        }

        .sidebar-logo {
            margin-top: rem(22px);
            height: rem(43px);
            width: rem(43px);
        }

